<template>
    <el-card>
        <div >
            <el-button style="float: left;" type="success" @click="queryAll" >查询</el-button>
            <el-button style="float: right;" type="primary" @click="preAdd">添加课程</el-button>
        </div>

        <div id="courseApp" >
        <el-table :data="courseList" v-if="Array.isArray(courseList)">
            <el-table-column label="课程ID" prop="couId"></el-table-column>
            <el-table-column label="课程名称" prop="couName"></el-table-column>
            <el-table-column label="学分" prop="couXf"></el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button @click="preUpdate(scope.row)" type="primary" circle icon="el-icon-edit"></el-button>
                    <el-button @click="deleteCourse(scope.row)" type="danger" circle icon="el-icon-delete"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <el-dialog :title="title" :visible.sync="showDialog">
            <el-form>
                <el-form-item label="课程ID">
                    <el-input v-model="cou.couId" :disabled="idDisabled" />
                </el-form-item>
                <el-form-item label="课程名称">
                    <el-input v-model="cou.couName" />
                </el-form-item>
                <el-form-item label="学分">
                    <el-input v-model="cou.couXf" />
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="addOrUpdate">{{ btnLabel }}</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
    </el-card>
</template>

<script>
export default {
    data() {
    return {
        courseList: [],
        showDialog: false,
        cou: {},
        title: '',
        btnLabel: '',
        idDisabled: false
    }
    },
    mounted() {
        // 组件挂载后自动查询数据
        this.queryAll();
    },
    methods: {
        addOrUpdate() {
            if ('新增' === this.btnLabel) {
                this.addCourse();
            } else {
                this.updateCourse();
            }
        },
        updateCourse() {
            this.$xhr.put('/course', this.cou).then(res => {
                if (res.status == 200) {
                    this.showDialog = false;
                    this.$message.success("修改成功！");
                    this.queryAll();
                } else {
                    this.$message.error(res);
                }
            });
        },
        preUpdate(row) {
            /*
                1. 修改对话框的标题
                2. 对话框中显示要修改的值
                3. 修改按钮的文字
                4. 判断是新增还是修改，调用不同的方法
            */
            this.title = "修改课程";
            this.cou = {...row}; // 创建一个新的对象副本，避免直接引用
            this.showDialog = true;
            this.btnLabel = "修改";
            this.idDisabled = true;
        },
        addCourse() {
            this.$xhr.post('/course', this.cou).then(res => {
                if (res.status == 200) {
                    this.showDialog = false;
                    this.$message.success("添加成功！");
                    this.queryAll();
                } else {
                    this.$message.error(res);
                }
            });
        },
        preAdd() {
            this.cou = {};
            this.title = "新增课程";
            this.btnLabel = '新增';
            this.idDisabled = false;
            // 弹出添加课程的对话框
            this.showDialog = true;
        },
        queryAll() {
            this.$xhr.post('/course/all').then(res => {
                // 确保返回的数据是数组格式
                this.courseList = Array.isArray(res.data) ? res.data : [];
            }).catch(error => { 
                this.$message.error("查询失败：" + error.message); 
            });
        },
        deleteCourse(row) {
            // 确保row存在且有couName属性
            const courseName = (row && row.couName) ? row.couName : '';
            this.$confirm(`确定要删除【${courseName}】吗？`, '警告', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                // 确保row存在且有couId属性
                if (row && row.couId) {
                    this.$xhr.delete('/course?couId=' + row.couId).then(res => {
                        if (res.status == 200) {
                            this.$message.success("删除成功！");
                            this.queryAll();
                        } else {
                            this.$message.error(res);
                        }
                    }).catch(error => {
                        this.$message.error("删除失败：" + error.message);
                    });
                } else {
                    this.$message.error("无效的课程数据");
                }
            }).catch(() => {
                // 用户取消删除
            });
        }
    }
}
</script>
