<template>
    <el-container style="height: 100vh;">
        <el-header>Header
            <el-button style="float: right;" type="primary" @click="logout">退出</el-button>
        </el-header>
        <el-container>
                <el-menu :collapse="isCollapse" router :default-active="activedIndex" class="el-menu-vertical-demo" >
                    <el-menu-item style="text-align: right;" @click="openOrClose">
                        <i :class="collapseIcon"></i>
                    </el-menu-item>
                    <el-submenu index="1">
                        <template slot="title">
                            <i class="el-icon-location"></i>
                            <span>导航一</span>
                        </template>
                        <el-menu-item-group>
                            <template slot="title">分组一</template>
                            <el-menu-item index="/CourseManager" @click="activedIndex = '/CourseManager'"> 课程管理 </el-menu-item>
                            <el-menu-item index="/UserManager" @click="activedIndex = '/UserManager'">用户管理</el-menu-item>
                        </el-menu-item-group>
                        <el-menu-item-group title="分组二">
                            <el-menu-item index="/activityManager" @click="activedIndex = '/activityManager'">活动管理</el-menu-item>
                        </el-menu-item-group>
                        <el-submenu index="1-4">
                            <template slot="title">选项4</template>
                            <el-menu-item index="1-4-1">选项1</el-menu-item>
                        </el-submenu>
                    </el-submenu>
                    <el-menu-item index="2">
                        <i class="el-icon-menu"></i>
                        <span slot="title">导航二</span>
                    </el-menu-item>
                    <el-menu-item index="3" disabled>
                        <i class="el-icon-document"></i>
                        <span slot="title">导航三</span>
                    </el-menu-item>
                    <el-menu-item index="4">
                        <i class="el-icon-setting"></i>
                        <span slot="title">导航四</span>
                    </el-menu-item>
                </el-menu>
            <el-main>
                <router-view/>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
export default {
    name: 'myWelcome',
    data() {
        return {
            collapseIcon:'el-icon-s-unfold',
            isCollapse:true,
            activedIndex: ''
        }
    },
    methods:{
        handleOpen(key, keyPath) {
            console.log('Menu opened:', key, keyPath);
        },
        handleClose(key, keyPath) {
            console.log('Menu closed:', key, keyPath);
        },
        logout(){
            sessionStorage.removeItem('token');
            this.$router.push({path:'/'});
        },
        openOrClose(){
            this.isCollapse= !this.isCollapse;
            if(this.isCollapse){
                this.collapseIcon='el-icon-s-unfold'
            }else{
                this.collapseIcon='el-icon-s-fold'
            }

        }

    }
}
</script>
<style>
.el-header,
.el-footer {
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
    line-height: 60px;
}

.el-aside {
    background-color: #D3DCE6;
    color: #333;
    /* text-align: center; */
    line-height: 200px;
}

.el-main {
    background-color: #E9EEF3;
    color: #333;
    /* text-align: center; */
    /* line-height: 160px; */
}

body>.el-container {
    margin-bottom: 40px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
    line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
    line-height: 320px;
}
.el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;
  }
</style>