<template>
    <el-card>
        <div>
            <el-button type="success" style="float: left;" @click="query">查询</el-button>
            <el-button type="primary" style="float: right;" @click="preAdd">新增活动</el-button>
        </div>
        <div>
            <el-table :data="activityList">
                <el-table-column label="活动名称" prop="name"></el-table-column>
                <el-table-column label="活动地点" prop="location"></el-table-column>
                <el-table-column label="开始时间" prop="startTime"></el-table-column>
                <el-table-column label="添加时间" prop="creationTime"></el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button @click="preUpdate(scope.row)" type="primary" circle icon="el-icon-edit"></el-button>
                        <el-button @click="deleteActivity(scope.row)" type="danger" circle
                            icon="el-icon-delete"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!-- 新增或修改的弹框-->
            <el-dialog :title="dialogTitle" :visible.sync="dialogFormVisible">
                <el-form>
                    <el-form-item label="活动名称" :label-width="formLabelWidth">
                        <el-input v-model="activity.name" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="活动地点" :label-width="formLabelWidth">
                        <el-input v-model="activity.location" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="开始时间" :label-width="formLabelWidth">
                        <el-date-picker v-model="activity.startTime" type="datetime" placeholder="选择日期时间"
                            value-format="yyyy-MM-dd HH:mm:ss">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="活动图片" :label-width="formLabelWidth">
                        <el-upload :action="uploadUrl" list-type="picture-card"
                            :on-preview="handlePictureCardPreview" :on-remove="handleRemove" :headers="headers"
                            :on-success="uploadSuccess" :file-list="fileList">
                            <i class="el-icon-plus"></i>
                        </el-upload>
                        <!-- 图片预览弹框-->
                        <el-dialog :visible.sync="dialogVisible" :modal="false">
                            <img width="100%" :src="dialogImageUrl" alt="活动图片" />
                        </el-dialog>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false">取 消</el-button>
                    <el-button type="primary" @click="addOrUpdate">{{ btnLabel }}</el-button>
                </div>
            </el-dialog>

        </div>
    </el-card>
</template>

<script>
export default {
    data() {
        return {
            activityList: [],
            dialogFormVisible: false,
            formLabelWidth: '120px',
            activity: {
                picIds: []
            },
            dialogImageUrl: '',
            dialogVisible: false,
            headers: {
                Authorization: `Bearer ${this.getToken()}`
            },
            fileList: [],
            dialogTitle: '',
            btnLabel: '',
            uploadUrl: process.env.VUE_APP_API_BASE_URL + '/actPic/saveActPic'
        }
    },
    mounted() {
        // 组件挂载后自动查询数据
        this.query();
    },
    methods: {
        addOrUpdate() {
            if ('新增活动' === this.btnLabel) {
                this.saveActivity();
            } else if ('修改活动' === this.btnLabel) {
                this.updateActivity();
            }
        },
        getToken() {
            return sessionStorage.getItem('token');
        },
        query() {
            this.$xhr.post('/activity/all').then(res => {
                this.activityList = res.data;
            });
        },
        preAdd() {
            this.dialogTitle = '新增活动';
            this.fileList = []; // 清空图片列表
            this.activity = {
                picIds: []
            };
            this.btnLabel = '新增活动';
            this.dialogFormVisible = true;
        },
        saveActivity() {
            this.$xhr.post('/activity/save', this.activity).then(res => {
                this.dialogFormVisible = false;
                if (res.code == 0) {
                    this.$message.success("保存成功！");
                    this.query();
                } else {
                    this.$message.error(res.msg);
                }
            });
        },
        handleRemove(file, fileList) {
            this.fileList = [ ...fileList ];
            //确认是否删除
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                // 删除图片
                this.deletePic(file);
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
                //取消删除时恢复图片列表
                this.fileList.push(file);
            })
        },
        deletePic(file) {
            this.$xhr.post('/actPic/deletePic', file).then(res => {
                if (res.code == 0) {
                    this.$message.success("删除成功！");
                    this.query();
                } else {
                    this.$message.error(res.msg);
                }
            });
        },
        handlePictureCardPreview(file) {
            this.dialogImageUrl = file.url;
            this.dialogVisible = true;
        },
        uploadSuccess(res) {
            // 确保 activity.picIds 存在且为数组
            if (!this.activity.picIds || !Array.isArray(this.activity.picIds)) {
                this.activity.picIds = [];
            }
            this.activity.picIds.push(res.data.id);
        },
        preUpdate(row) {
            this.btnLabel = '修改活动';
            this.dialogTitle = '修改活动';
            this.fileList = []; // 清空图片列表
            this.activity = { ...row };
            // 确保 picIds 字段存在
            if (!this.activity.picIds || !Array.isArray(this.activity.picIds)) {
                this.activity.picIds = [];
            }
            this.$xhr.get(`/actPic/getPics/${row.id}`).then(res => {
                var list = res.data;
                for (var i = 0; i < list.length; i++) {
                    var pic = list[i];
                    var f = { url: process.env.VUE_APP_API_BASE_URL + pic.picPath };
                    this.fileList.push(f);
                }
                this.dialogFormVisible = true;
            });
        },
        deleteActivity(row) {
            this.$confirm('此操作将永久删除该活动, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                console.log(row);
                this.$xhr.post('/activity/delete', row).then(res => {
                    if (res.code == 0) {
                        this.$message.success("删除成功！");
                        this.query();
                    }else {
                        this.$message.error(res.msg);
                    }
                })
            })
        },
        updateActivity() {
            this.$xhr.post('/activity/update', this.activity).then(res => {
                this.dialogFormVisible = false;
                if (res.code == 0) {
                    this.$message.success("修改成功！");
                    this.query();
                } else {
                    this.$message.error(res.msg);
                }
            });
        }
    }
}
</script>