import Vue from 'vue'
import App from './App.vue'
import router from './router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import axios from 'axios'

axios.defaults.baseURL = process.env.VUE_APP_API_BASE_URL
axios.defaults.timeout = 5000
// 挂载到 Vue 原型链，组件内通过 this.$axios 调用

// 请求拦截器
axios.interceptors.request.use(
  config => {
    const token = sessionStorage.getItem('token');
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

// 响应拦截器
axios.interceptors.response.use(
  response => {
    return response.data;
  },
  error => {
    console.log(error);
    if (error.response) {  // 服务器有响应
      var status = error.response.status;
      switch (status) {
        case 400:
          Vue.prototype.$message.error(`请求错误: ${error.message}`);
          break;
        case 401:
          Vue.prototype.$message.error('未授权，请重新登录')
          // 可以跳转到登录页
          break
        case 403:
          Vue.prototype.$message.error('拒绝访问')
          break
        case 404:
          Vue.prototype.$message.error('请求地址不存在')
          break
        case 500:
          Vue.prototype.$message.error('服务器内部错误')
          break
        case 502:
          Vue.prototype.$message.error('网关错误')
          break
        case 503:
          Vue.prototype.$message.error('服务不可用')
          break
        default:
          Vue.prototype.$message.error(`网络错误: ${error.message}`)
      }
    } else if (error.request) {  // 请求发送了，但没收到响应
      Vue.prototype.$message.error(error);
    } else {  // 其他错误
      Vue.prototype.$message.error(`请求配置错误: ${error.message}`)
    }
    return new Promise(() => { });  // 这样then不会执行，也不会抛出异常
  }
);


Vue.prototype.$xhr = axios

Vue.use(ElementUI);

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
