import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/views/Login.vue'
import Welcome from '@/views/Welcome.vue'
import CourseManager from '@/views/CourseManager.vue'
import UserManager from '@/views/UserList.vue'
import NewUser from '@/views/NewUser.vue'
import ActivityManager from '@/views/ActivityManager.vue'

Vue.use(Router);

var router = new Router({
    routes: [
        {
            path: '/login',
            name: 'Login',
            component: Login,
        },
        {
            path: '/',
            redirect: '/login'
        },
        {
            name: 'Welcome',
            path: '/welcome',
            component: Welcome,
            children: [
                {
                    name: 'CourseManager',
                    path: '/courseManager',
                    component: CourseManager
                },
                {
                    name: 'UserManager',
                    path: '/userManager',
                    component: UserManager
                },
                {
                    name: 'ActiveManager',
                    path: '/activityManager',
                    component: ActivityManager
                }
            ]
        },
        {
            name: 'NewUser',
            path: '/newUser',
            component: NewUser
        }
    ]
});

//路由守卫
router.beforeEach((to, from, next) => {
    const token = sessionStorage.getItem("token");
    if (to.path != '/login' && to.path !== '/newUser' && !token) {
        next('/login');
    } else {
        next();
    }
});


export default router;