<template>
    <div id="login-div">
        <el-row type="flex" justify="center" align="middle" style="height:100vh;">
            <el-col class="login-panel">
                <el-card shadow="always">
                    <h3>软约管理系统登录</h3>
                    <el-form>
                        <el-form-item>
                            <el-input v-model="user.username" placeholder="请输入用户名"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-input type="password" v-model="user.password" placeholder="请输入密码"></el-input>
                        </el-form-item>
                        <el-form-item>
                                <el-button type="success" style="width: 47%;" @click="login"
                                    v-loading.fullscreen.lock="fullscreenLoading" element-loading-text="拼命登录中"
                                    element-loading-spinner="el-icon-loading"
                                    element-loading-background="rgba(0, 0, 0, 0.8)">登录</el-button>
                                <el-button type="primary" style="width: 47%;" @click="addUser"
                                    element-loading-background="rgba(0, 0, 0, 0.8)">注册</el-button>
                        </el-form-item>
                    </el-form>
                </el-card>
            </el-col>
        </el-row>
    </div>
</template>

<script>
export default {
    name: 'userLogin',
    data() {
        return {
            user: {},
            fullscreenLoading: false
        }
    },
    methods: {
        login() {
            this.fullscreenLoading = true;
            this.$xhr.post('/login', this.user).then(res => {
                if (res.code === 0) {
                    //保存token
                    sessionStorage.setItem('token', res.data);
                    this.$message.success("登录成功!");
                    this.$router.push({ name: 'Welcome' });
                } else {
                    this.$message.error(res.msg);
                }
                this.fullscreenLoading = false;
            }).catch(err => {
                this.$message.error(err);
                this.fullscreenLoading = false;
            });
        },
        addUser() {
            this.$router.push({ name: 'NewUser' });
        }
    }
}
</script>

<style scoped>
#login-div {
    background-color: rgb(210 231 252);
}


.login-panel {
    width: 340px;
    padding: 10px;
    border-radius: 10px;
}

h3 {
    text-align: center;
    color: rgb(64, 158, 255);
}

a {
    font-size: small;
    display: block;
    text-align: center;
    color: rgb(64, 158, 255);
}
</style>