<template>
    <div style="padding: 20px; background-color: #fff; border-radius: 10px;">
        <el-button type="success" @click="query">查询</el-button>
        <el-table :data="userList">
            <el-table-column prop="id" label="用户ID" />
            <el-table-column prop="username" label="用户名" />
            <el-table-column prop="lastLoginTime" label="最后登录" />
            <el-table-column prop="creationTime" label="创建时间" />
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button @click="preUpdate(scope.row)" type="primary" circle icon="el-icon-edit"></el-button>
                    <el-button @click="deleteCourse(scope.row)" type="danger" circle icon="el-icon-delete"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-dialog :title="title" :visible.sync="showDialog">
            <el-form>
                <el-form-item label="用户名">
                    <el-input v-model="user.username" :disabled="idDisabled" />
                </el-form-item>
                <el-form-item label="用户密码">
                    <el-input v-model="user.password" type="password" />
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="update">修改</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: 'UserManager',
    data() {
        return {
            userList: [],
            user: {},
            title: '',
            showDialog: false,
            idDisabled: false
        }
    },
    mounted() {
        // 组件挂载后自动查询数据
        this.query();
    },
    methods: {
        query() {
            this.$xhr.get('/user/all').then(res => {
                this.userList = res.data;
            });
        },
        deleteCourse(row) {
            this.user = row;
            this.$confirm('确定要删除【' + row.username + '】吗？', '警告', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$xhr.post('/user/deleteUser', this.user).then(res => {
                    if (res.code == 0) {
                        this.$message.success("删除成功！");
                        this.query();
                    }
                })
            })
        },
        preUpdate(row) {
            /*
               1. 修改对话框的标题
               2. 对话框中显示要修改的值
               3. 修改按钮的文字
               4. 判断是新增还是修改，调用不同的方法
           */
            this.title = "修改用户";
            this.user = { ...row }; // 创建一个新的对象副本，避免直接引用
            this.user.password = '';
            this.showDialog = true;
            this.idDisabled = true;
        },
        update() {
             this.$xhr.post('/user/update', this.user).then(res => {
                if (res.code == 0) {
                    this.$message.success("修改成功！");
                    this.showDialog = false;
                    this.query();
                } else {
                    this.showDialog = false;
                    this.$message.error(res.msg);
                }
            });
        },
    }
}
</script>

<style></style>
